@import "colors";

.product-ref {
  background-color: $lighten;
  border-radius: 2px;
  padding: 1px;
  display: inline-block;
  color: $darker;
  font-family: 'rawline', sans-serif;
  font-weight: 600;
  font-size: 0.825em;
  line-height: initial;
}
.exception_badge{
  background-color: $red;
  height: 20px;
  width: 20px;
  position: absolute;
  border-radius: 50%;
  top: -10px;
  left: -10px;
  cursor: pointer;
  &:hover > div{
    display: block;
  }
}
.tooltip{
  position: absolute;
  width: 200px;
  font-size: 0.875em;
  background-color: $dark;
  color: $white;
  padding: 10px;
  text-align: center;
  left: 35px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  display: none;
  z-index: 10;
  &:before{
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 10px 8px;
    border-color: transparent transparent $dark transparent;
    content: '';
    position: absolute;
    display: block;
    left: 50%;
    top: -10px;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
  }
}
.left-arrow-tooltip {
  position: absolute;
  width: 200px;
  font-size: 0.875em;
  background-color: $dark;
  color: $white;
  padding: 10px;
  text-align: center;
  left: 35px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  display: none;
  z-index: 10;
  &:before{
    border-style: solid;
    content: '';
    position: absolute;
    display: block;
    top: calc(50% - 10px);
    right: 100%;
    border-width: 10px;
    border-color: transparent #333 transparent transparent;
  }
}
